<script>
export default {
  name: "LampsComponent",
  methods: {
    onButtonClick() {
      alert("Molt bé! Has perseguit el botó i l'has aconseguit clicar");
    },
    hoverHandler(buttonRef, isHovered) {
      const button = this.$refs[buttonRef];
      if (button) {
        if (isHovered) {
          button.style.transform = "translateX(100px)";
          button.style.transition = "transform 0.3s ease";
        } else {
          this.timeoutRefs[buttonRef] = setTimeout(() => {
            button.style.transform = "translateX(0)";
          }, 2000);
        }
      }
    },
  },
  beforeDestroy() {
    Object.values(this.timeoutRefs).forEach((timeout) => clearTimeout(timeout));
  },
};
</script>

